import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import InnerContainer from '../../../components/Container/InnerContainer';
import { db } from '../../../config/firebaseConfig';
import { UserContext } from '../../../context/UserContext';


import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const Search = () => {
  const [ setting ] = useContext(UserContext)
  const [ userlist, setUserlist ] = useState(false)

  const onClickLookUp = (title) => {
    db.collection('주일예배').doc(title).collection('20210815').orderBy("submitTime").get().then((querySnapshot) => {
      let tempArray = []
       querySnapshot.forEach((doc) => {
         tempArray.push({
          id: doc.id,
          ...doc.data()
         })
      })
      setUserlist(tempArray)
    }).catch(error => {
      console.log(error)
    })
  }

  const converTime = (timestamp) => {
    let date = new Date(timestamp * 1000)
    let hours = date.getHours();
    let minutes = "0" + date.getMinutes();
    let seconds = "0" + date.getSeconds();
    let formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
    return formattedTime
  }

  return (
    <InnerContainer>
      <LoockupContainer>
        <LoockupBtnWrapper>
          <LookupButton onClick={() => onClickLookUp('1부예배')}>1부예배</LookupButton>
          <LookupButton onClick={() => onClickLookUp('2부예배')}>2부예배</LookupButton>
          <LookupButton onClick={() => onClickLookUp('3부예배')}>3부예배</LookupButton>
          <LookupButton onClick={() => onClickLookUp('4부예배')}>4부예배</LookupButton>
          <LookupButton onClick={() => onClickLookUp('6부예배')}>6부예배</LookupButton>
        </LoockupBtnWrapper>
      </LoockupContainer>

      <TableContainer component={Paper}>
        <Table aria-label="simple table" style={{minWidth: 250}}>
          <TableHead>
            <TableRow>
              <TableCell align="center">순서</TableCell>
              <TableCell align="center">이름</TableCell>
              <TableCell align="center">직분</TableCell>
              <TableCell align="center">시간</TableCell>
            </TableRow>
          </TableHead>
          {userlist && (
            <>
            {userlist.map((user, index) => (
              <TableBody>
                <TableRow key={index}>
                  <TableCell align="center">{index}</TableCell>
                  <TableCell align="center">{user.name}</TableCell>
                  <TableCell align="center">{user.position}</TableCell>
                  <TableCell align="center">{converTime(user.submitTime)}</TableCell>
                </TableRow>
              </TableBody>
            ))}  
            </>
          )}
        </Table>
      </TableContainer>
    </InnerContainer>
  );
}

const LoockupContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const LoockupBtnWrapper = styled.div`
  padding: 2rem 0;
  margin-bottom: 1rem;
`;

const LookupButton = styled.button`
  border: 2px solid #228be6;
  border-radius: 10px;
  padding: 1rem 1.6rem;
  margin: 0 1rem;
  background: #fff;
`;

export default Search;